.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header_logo{
    width: 100px;    
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;

}

.header_search{
    display: flex;
    align-items: center;
    flex: 1;
    border-radius: 24px;
}

.header_search_input{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%; 
}

.header_search_icon{
    background-color: #cd9042;
    padding: 5px;
    height: 22px !important;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_option{
    color: white;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header_option_line_one{
    font-size: 10px;
}
.header_option_line_two{
    font-size: 13px;
    font-weight: 800;
}

.header_option_basket{
    display: flex;
    align-items: center;
    color: white;
}

.header_basket_count{
    margin-left: 10px;
    margin-right: 10px;
}




































